<template>
  <div style="margin-top: 60px;text-align: center;">


    <hr style="height:1px;border:none;border-top:1px dashed #0066CC; margin-left: 200px; margin-right: 200px"/>

    <h1>Android</h1>

    <p><a href="https://github.com/Blankj/AndroidUtilCode/blob/master/utilcode/README-CN.md" target="view_window">
      <el-button size="mini" type="primary" round>Android </el-button>
    </a></p>


    <hr style="height:1px;border:none;border-top:1px dashed #0066CC; margin-left: 200px; margin-right: 200px"/>

    <p>
      <router-link to="Git"  arget="view_window">
      <el-button size="mini" type="primary" round>Git</el-button></router-link></p>

    <hr style="height:1px;border:none;border-top:1px dashed #0066CC; margin-left: 200px; margin-right: 200px"/>


    <h1></h1>


    <div>
      <el-button @click="getNowTime" type="success" size="medium"></el-button> &nbsp;&nbsp; ======>> &nbsp;&nbsp;
      <el-input v-model="now_time" placeholder="" style="width: 180px;margin: 20px"></el-input>

      <!--<p><el-input v-model="input" placeholder="" style="margin: 0px"></el-input></p>-->
      <!--<p><el-button type="success" size="medium"></el-button></p>-->
    </div>

    <div>
      <el-input v-model="bj_time" placeholder="" style="width: 180px;margin: 20px"></el-input>
      <el-button @click="getBJTime" type="success" size="medium">__ >></el-button>
      <el-input v-model="bj_time_success" placeholder="" style="width: 180px;margin: 20px"></el-input>
    </div>

    <div>
      <el-input v-model="unix_time" placeholder="" style="width: 180px;margin: 20px"></el-input>
      <el-button @click="getUnixTime" type="success" size="medium">Unix >></el-button>
      <el-input v-model="unix_time_success" placeholder="" style="width: 180px;margin: 20px"></el-input>
    </div>

    <hr style="height:1px;border:none;border-top:1px dashed #0066CC; margin-left: 200px; margin-right: 200px"/>

  </div>
</template>

<script>
  export default {
    name: "Home",

    data() {
      return {
        resultStatus: false,
        now_time: '',
        bj_time: '',
        bj_time_success: '',
        unix_time: '',
        unix_time_success: '',
        data: [{
          'result': '',
          'taskId': '',
        }]
      }
    },
    methods: {
      getNowTime() {
        var now = new Date();
        now = now.getTime();
        console.log(now);
        this.now_time = now;
      },
      getBJTime() {
        // var t = 787986456465;  // 19701
        let time = new Date(Number(this.bj_time));
        console.log(time);

        this.bj_time_success = this.timestampToTime(Number(this.bj_time));

        let date = new Date(time); //10*1000131000
        let Y = date.getFullYear() + '-';
        let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        let D = this.change(date.getDate()) + ' ';
        let h = this.change(date.getHours()) + ':';
        let m = this.change(date.getMinutes()) + ':';
        let s = this.change(date.getSeconds());
        this.bj_time_success = Y + M + D + h + m + s;

      },
      getUnixTime() {
        // var t = "2017-12-08 20:5:30";  // 0.
        time = this.unix_time;
        let time = time.replace(new RegExp(/-/gm), "/"); //'-''/'
        let t = new Date(time).getTime();  // Fri Dec 08 2017 20:05:30 GMT+0800 ()
        console.log(t);
        this.unix_time_success = t; // 
      },


      timestampToTime(time) {
        let date = new Date(this.bj_time); //10*1000131000
        let Y = date.getFullYear() + '-';
        let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        let D = this.change(date.getDate()) + ' ';
        let h = this.change(date.getHours()) + ':';
        let m = this.change(date.getMinutes()) + ':';
        let s = this.change(date.getSeconds());
        return Y + M + D + h + m + s;
      }
      ,
      change(t) {
        if (t < 10) {
          return "0" + t;
        } else {
          return t;
        }
      }
      ,


    }
  }

  function timestampToTime(timestamp) {
    var date = new Date(timestamp);//10*1000131000
    Y = date.getFullYear() + '-';
    M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
    D = date.getDate() + ' ';
    h = date.getHours() + ':';
    m = date.getMinutes() + ':';
    s = date.getSeconds();
    return Y + M + D + h + m + s;
  }
</script>

<style>
  /*.el-input {*/
  /*width: 180px;*/
  /*margin: 20px;*/
  /*}*/
</style>



